import Vue from 'vue'
import Vuex from 'vuex';
Vue.use(Vuex);

const state={
  user:{},   // 
  //bindMobile:'',   // 
  vaccine:'',  // 
  department:'',  // 
  isMobile:{
    isWx: false,  //
    isXdm: false  // 
  },
  currentCity:'',  // 
  currentAreaCode:'', // 
  awardPoints:0, // 
  messageNum:0, // 
  token:'',
  selectedMember:{}, //
  submitInfo:{}, // 
  position:{},//
  isWxConfigReady: false,  //
  dogInjuryInfo:{} , // 

  HKHpvInfo:{}, //hpv
  uFrom:'', // 
  currentVaccineCode:'',//
  cities:[],//
}

const mutations = {
  setUser(state,user){
    state.user = user;
  },
  /*setBindMobile(state,bindMobile){
    state.user.mobile = bindMobile;
  },*/
  setVaccine(state,vacc){
    state.vaccine = vacc;
  },
  setDepa(state,depa){
    state.department = depa;
  },
  setUa(state,ua){
    if(ua=="wx"){
      state.isMobile.isWx = true;
    }else if(ua=="xdm"){
      state.isMobile.isXdm = true;
    }
  },
  setCurrentCity(state,e){
    state.currentCity = e;
  },
  setCurrentAreaCode(state,e){
    state.currentAreaCode = e;
  },
  setAwardPoints(state,e){
    state.awardPoints = e;
  },
  setMessageNum(state,e){
    state.messageNum = e;
  },
  setToken(state,e){
    state.token = e;
  },
  setSelectedMember(state,e){
    state.selectedMember = e;
  },
  setSubmitInfo(state,e){
    state.submitInfo = e;
  },
  setPosition(state,e){
     state.position=e;
  },
  setDogInjuryInfo(state,e){
    state.dogInjuryInfo = e;
  },

  setHKHpvInfo(state,e){
    state.HKHpvInfo = e;
  },

  setUFrom(state,e){
    state.uFrom = e;
  },

  setCurrentVaccineCode(state,e){
    state.currentVaccineCode=e;
  },
  setCities(state,e){
    state.cities=e;
  }
}

export default new Vuex.Store({
  state,
  mutations
})



