import baseCfg from '../../config/baseConfig'
export default {
  created(){
    let shareInfo = {};
    shareInfo.link = baseCfg.appInfo.shareBaseUrl + '?target=examinationSearch?fa=wx';
    shareInfo.title = '';
    shareInfo.desc = '';
    this.$wxShare(shareInfo);
  }
}



