import axios from 'axios'
import aipConfig from '../../config/apiConfig'
import { mapState } from 'vuex';
import {wxShare} from '../../config/setWxJSSDK'

export default{
  data(){
    return{
      membersList:[]
    }
  },
  computed:mapState(["user"]),
  methods:{
    /**
     * 
     */
    getMembersList(){
      axios.get(aipConfig.findByUserId).then((res) => {
        let list = res.data.data;
        list.forEach(function (e, i) {

          if(e.sex == 1) e.sex = '';
          else if(e.sex == 2) e.sex = '';
          else e.sex = '';

          switch(e.relationType){
            case 1:
              e.relationType = '';
              break;
            case 2:
              e.relationType = '';
              break;
            case 3:
              e.relationType = '';
              break;
            case 4:
              e.relationType = '';
              break;
            case 5:
              e.relationType = '';
              break;
            case 6:
              e.relationType = '';
              break;
            case 7:
              e.relationType = '';
              break;
          }
          e.birthday = moment(e.birthday).format("YYYY/MM/DD");
        })
        this.membersList = list;
      }).catch((e) => {
        console.log(e)
      })
    },

    addMember(){
      this.$router.push({ path: '/addOrEditMember'})
    },

    toDetail(id){
      this.$router.push({ path: '/addOrEditMember', query: { id: id}})
    }

  },

  created(){
    wxShare();
    this.getMembersList();
  }
}



