
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default{
components: {
Group,
Cell
},
data(){
return{
show: false
}
},
props:{
item:{},
index:''
},
methods:{
formatRecord(){
for ( let i in this.item.inoculations) {
var record = this.item.inoculations[i];
if (record.status == 0) {
record.state = "";
} else if (record.status == 1) {
record.state = "";
} else if (record.status == 2) {
record.state = "";
} else {
record.state = "";
}
switch(record.inoculateIndex)
{
case 1:
record.inoculateIndex = "";
break;
case 2:
record.inoculateIndex = "";
break;
case 3:
record.inoculateIndex = "";
break;
case 4:
record.inoculateIndex = "";
break;
default:
record.inoculateIndex = "";
}
let tips_time = moment(record.inoculateTime).subtract(record.subscribeDays,"day").format("YYYYMMDD");
record.tips = ""+ tips_time + "";
record.inoculateTime = moment(record.inoculateTime).format("YYYYMMDD");

}
},
toVaccine(vaCode,depaCode){
this.$router.push({ path: '/vaccine', query: { code: vaCode, depaCode: depaCode }})
}
},
created () {
if(this.index==0) this.show=true;
this.formatRecord();
}
}



